body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 23px;
}

.people {
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(4, 1fr);
  gap: 40px 30px;
  margin-top: 30px;
  margin-left: 305px;
  margin-right: 305px;
  display: grid;
}

@media (width <= 1000px) {
  .people {
    margin-left: 55px;
    margin-right: 55px;
  }
}

@media (width <= 750px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    margin-left: 160px;
    margin-right: 160px;
  }
}

@media (width <= 500px) {
  .people {
    margin-left: 35px;
    margin-right: 35px;
  }
}

.person__photo {
  position: relative;
}

.person__photo-img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.person__photo-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 600;
  position: absolute;
  top: 82px;
  left: 165px;
}

.person__details-name-link {
  color: #2f4f4f;
  line-height: 23px;
}
